<template>
  <div>
    <h5>ConfirmationService</h5>
    <p>
      ConfirmDialog is controlled via the <i>ConfirmationService</i> that needs to be installed globally before the
      application instance is created.
    </p>

    <DocSectionCode :code="usageCode" importCode />

    <h5>Import</h5>
    <DocSectionCode :code="importCode" importCode />

    <h5>Getting Started</h5>
    <p>
      ConfirmDialog is displayed by calling the <i>require</i> method of the <i>$confirm</i> instance by passing the
      options to customize the Dialog. Suggested location of the Dialog is the main application component where it can
      be shared by any component within the application.
    </p>
    <DocSectionCode :code="baseCode" />
    <DocSectionCode :code="baseCode2" importCode />

    <h5>Close Confirmation</h5>
    <p>The dialog can also be hidden programmatically using the <i>close</i> method.</p>
    <DocSectionCode :code="closeConfirmationCode" importCode />

    <h5>Confirmation Options</h5>
    <p>ConfirmDialog can be customized with various options listed here.</p>
    <div class="doc-tablewrapper">
      <table class="doc-table">
        <thead>
          <tr>
            <th>Name</th>
            <th>Type</th>
            <th>Default</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>message</td>
            <td>string</td>
            <td>null</td>
            <td>Message of the confirmation.</td>
          </tr>
          <tr>
            <td>group</td>
            <td>string</td>
            <td>null</td>
            <td>
              Optional key to match the key of the confirmation, useful to target a specific confirm dialog instance.
            </td>
          </tr>
          <tr>
            <td>icon</td>
            <td>string</td>
            <td>null</td>
            <td>Icon to display next to the message.</td>
          </tr>
          <tr>
            <td>header</td>
            <td>string</td>
            <td>null</td>
            <td>Header text of the dialog.</td>
          </tr>
          <tr>
            <td>accept</td>
            <td>Function</td>
            <td>null</td>
            <td>Callback to execute when action is confirmed.</td>
          </tr>
          <tr>
            <td>reject</td>
            <td>Function</td>
            <td>null</td>
            <td>Callback to execute when action is rejected.</td>
          </tr>
          <tr>
            <td>acceptLabel</td>
            <td>string</td>
            <td>null</td>
            <td>
              Label of the accept button. Defaults to PrimeVue
              <router-link to="/locale">Locale</router-link> configuration.
            </td>
          </tr>
          <tr>
            <td>rejectLabel</td>
            <td>string</td>
            <td>null</td>
            <td>
              Label of the reject button. Defaults to PrimeVue
              <router-link to="/locale">Locale</router-link> configuration.
            </td>
          </tr>
          <tr>
            <td>acceptIcon</td>
            <td>string</td>
            <td>null</td>
            <td>Icon of the accept button.</td>
          </tr>
          <tr>
            <td>rejectIcon</td>
            <td>string</td>
            <td>null</td>
            <td>Icon of the reject button.</td>
          </tr>
          <tr>
            <td>acceptClass</td>
            <td>string</td>
            <td>null</td>
            <td>Style class of the accept button.</td>
          </tr>
          <tr>
            <td>rejectClass</td>
            <td>string</td>
            <td>null</td>
            <td>Style class of the reject button.</td>
          </tr>
          <tr>
            <td>blockScroll</td>
            <td>boolean</td>
            <td>true</td>
            <td>Whether background scroll should be blocked when dialog is visible.</td>
          </tr>
          <tr>
            <td>defaultFocus</td>
            <td>string</td>
            <td>null</td>
            <td>Element to receive the focus when the dialog gets visible, valid values are "accept" and "reject".</td>
          </tr>
        </tbody>
      </table>
    </div>

    <h5>Properties</h5>
    <p>
      Any property as style and class are passed to the main container element. Following are the additional properties
