          </tr>
        </thead>
        <tbody>
          <tr>
            <td>group</td>
            <td>string</td>
            <td>null</td>
            <td>
              Optional key to match the key of the confirmation, useful to target a specific confirm dialog instance.
            </td>
          </tr>
        </tbody>
      </table>
    </div>

